import React, {useState, useEffect, useRef} from 'react'
import classes from './jila.module.scss'




const Jila = () => {
  const [boards, setBoards] = useState([
    {id:1, title:'Сделать', items:[
      {id:1,title:'React'},{id:2,title:'Redax'},{id:3,title:'React Native'}
    ]},
    {id:2, title:'Проверить', items:[
      {id:4,title:'Angular'},{id:5,title:'Vue'},{id:6,title:'PureJS'}
    ]},
    {id:3, title:'Сделано', items:[
      {id:7,title:'PHP'},{id:8,title:'Python'},{id:9,title:'Ruby'}
    ]}
  ])

  const [currentBoard, setCurrentBoard] = useState(null)
  const [currentItem, setCurrentItem] = useState(null)


  function dropCartHandler(e, board) {
    const currentId = board.items.map(item => item.id)
    if (!currentId.includes(currentItem.id)) {
      board.items.push(currentItem)
             const currentIndex = currentBoard.items.indexOf(currentItem)
             currentBoard.items.splice(currentIndex, 1)
             setBoards(boards.map(b => {
                if (b.id === board.id) {
                   return board
                }
                if (b.id === currentBoard.id) {
                   return currentBoard
                }
                return b
             }))
          }
  }

  function dragOverHandler(e) {
    e.preventDefault();
    if(e.target.className == classes.item){
      e.target.style.boxShadow = '0 4px 3px white'
    }
  }

  function dragLeaveHandler(e) {
    e.target.style.boxShadow = 'none'
  }

  function dragStartHandler(e, board, item) {
    setCurrentBoard(board)
    setCurrentItem(item)
  }

  function dragEndHandler(e) {
    e.target.style.boxShadow = 'none'
  }

  function dropHandler(e,board, item) {
    e.preventDefault();
    const currentIndex = currentBoard.items.indexOf(currentItem)
    currentBoard.items.splice(currentIndex,1)
    const dropIndex = board.items.indexOf(item)
    board.items.splice(dropIndex + 1 , 0, currentItem)
    setBoards(boards.map(b => {
      if(b.id === board.id) {
        return board
      }
      if(b.id === currentBoard.id) {
        return currentBoard
      }
      return b
    }))
    e.target.style.boxShadow = 'none'
  }

  return (
    <div className={classes.wrapper}>
        {boards.map((board,index) =>
          <div
            onDragOver={(e)=>dragOverHandler(e)}
            onDrop={(e)=>dropCartHandler(e,board)}
            key={board.id}
            className={classes.board}>
            <div className={classes.board__title}>{board.title}</div>
            {board.items.map((item,index) =>
              <div
              onDragOver={(e)=>dragOverHandler(e)}
              onDragLeave={(e)=>dragLeaveHandler(e)}
              onDragStart={(e)=>dragStartHandler(e,board,item)}
              onDragEnd={(e)=>dragEndHandler(e)}
              onDrop={(e)=>dropHandler(e,board,item)}
              draggable={true}
              key={item.id}
              className={classes.item}
              >{item.title}</div>
            )}
          </div>
        )}
    </div>
  )

}

export default Jila;
